import React, { Component } from "react";
import { Link } from "react-router-dom";

class ProjectGroup extends Component {
	render() {
		return (
			<div className="unit double">
				<hr />
				<br />
				<div className="margin" id={this.props.section}></div>
				<h3 className="cate">
					<strong>{this.props.name}</strong>
				</h3>
				<hr />
				<ul className="project">
					{this.props.data.map(proj => (
						<PageUnit
							key={proj[3]}
							title={proj[0]}
							description={proj[1]}
							imageUrl={proj[2]}
							link={proj[3]}
							external={proj[4]}
						/>
					))}
				</ul>
			</div>
		);
	}
}

class PageUnit extends Component {
	inner = (
		<li>
			<div className="img">
				<img
					src={`/Images/project/${this.props.imageUrl}`}
					alt={this.props.imageUrl}
				/>
			</div>
			<div className="details">
				<h4>{this.props.title}</h4>
				<br />
				<p>{this.props.description}</p>
			</div>
		</li>
	);

	render() {
		if (this.props.external === 2)
			return (
				<a
					href={this.props.link}
					target="_blank"
					rel="noopener noreferrer"
				>
					{this.inner}
				</a>
			);
		else if (this.props.external === 1)
			return (
				<a
					href={`/Images/project/${this.props.link}`}
					target="_blank"
					rel="noopener noreferrer"
				>
					{this.inner}
				</a>
			);
		return <Link to={`projects/${this.props.link}`}>{this.inner}</Link>;
	}
}

export default ProjectGroup;
