import React, { Component } from "react";
import { Link } from "react-router-dom";

import NavLinks from "./navLinks";

import "./nav.scss";

class Nav extends Component {
	openCloseMobileMenu = () => {
		const main = document.getElementById("root").classList;
		if (main.contains("openMobileMenu")) main.remove("openMobileMenu");
		else main.add("openMobileMenu");
	};

	render() {
		return (
			<nav id="nav">
				<Link to="/">
					<div id="logo"></div>
				</Link>
				<Link to="/">
					<h1 id="siteName">Theophilus</h1>
				</Link>

				<NavLinks title={this.props.title} />

				<div id="mobileNav">
					<div id="blocker" onClick={this.openCloseMobileMenu}></div>

					<ul id="panel">
						<NavLinks title={this.props.title} mobile />
					</ul>
				</div>

				<div
					onClick={this.openCloseMobileMenu}
					className="navBtn"
					id="mobileMenuButton"
				></div>
			</nav>
		);
	}
}

export default Nav;
