import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./navLinks.scss";

class NavLinks extends Component {
	setActive = title => {
		if (this.props.title === title) return "active";
		return "";
	};

	closeMobileMenu = () => {
		document.getElementById("root").classList.remove("openMobileMenu");
		// console.log("clicked")
	};

	project = () => {
		const proj01 = (
			<Link to="/projects">
				<li
					onClick={this.closeMobileMenu()}
					className={this.setActive("Projects")}
				>
					Projects
				</li>
			</Link>
		);

		const proj02 = (
			<li
				onClick={this.closeMobileMenu()}
				className={this.setActive("Projects")}
			>
				Projects
				<ul id="lv2" className="lv2">
					<Link to="/projects#web">
						<li>Web</li>
					</Link>
					<Link to="/projects#logo">
						<li>Logos and Banners</li>
					</Link>
					<Link to="/projects#model">
						<li>3D</li>
					</Link>
					<Link to="/projects#others">
						<li>Electronics</li>
					</Link>
				</ul>
			</li>
		);

		return this.props.mobile ? proj01 : proj02;
	};

	render() {
		return (
			<ul id="navLinks">
				<Link to="">
					<li
						onClick={this.closeMobileMenu()}
						className={this.setActive("Home")}
					>
						Home
					</li>
				</Link>
				<Link to="/about">
					<li
						onClick={this.closeMobileMenu()}
						className={this.setActive("About")}
					>
						About Me
					</li>
				</Link>
				{this.project()}
				<Link to="/contact">
					<li
						onClick={this.closeMobileMenu()}
						className={this.setActive("Contact")}
					>
						Contact
					</li>
				</Link>
			</ul>
		);
	}
}

export default NavLinks;
